         <li class=\"splide__slide\">\n                            <a\n                                :href=\"route('hostinvitations.index')\"\n                                :class=\"{ active: active == 'invitation' }\"\n                                ref=\"invitation\"\n                            >\n                                <span v-if=\"screenWidth < 768\">\n                                    <img src=\"/assets/hosthome/invitation.svg\" alt=\"\" />\n                                </span>\n                                Invitation\n                            </a>\n                        </li>\n                        <li class=\"splide__slide\">\n                            <a\n                                :href=\"route('invitedguests.custom', invi?.slug ?? '')\"\n                                :class=\"{ active: active == 'guests' }\"\n                                ref=\"guests\"\n                            >\n                                <span v-if=\"screenWidth < 768\">\n                                    <img src=\"/assets/invitation/guests.svg\" alt=\"\" />\n                                </span>\n                                Guest Book\n                            </a>\n                        </li>\n                        <li class=\"splide__slide\">\n                            <a\n                                :href=\"route('gallery.index')\"\n                                :class=\"{ active: active == 'gallery' }\"\n                                ref=\"gallery\"\n                            >\n                                <span v-if=\"screenWidth < 768\">\n                                    <img src=\"/assets/hosthome/gallery.svg\" alt=\"\" />\n                                </span>\n                                Gallery\n                            </a>\n                        </li>\n                        <li class=\"splide__slide\">\n                            <a\n                                :href=\"route('reports.index')\"\n                                :class=\"{ active: active == 'reports' }\"\n                                ref=\"reports\"\n                            >\n                                <span v-if=\"screenWidth < 768\">\n                                    <img src=\"/assets/hosthome/reports.svg\" alt=\"\" />\n                                </span>\n                                Reports\n                            </a>\n                        </li>\n                    </ul>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    props: [\"link\", \"invitation\"],\n    data() {\n        return {\n            active: this.link,\n            invi: this.invitation,\n            screenWidth: window.innerWidth,\n            cont: null,\n        };\n    },\n    methods: {\n        setSplider() {\n            this.$nextTick(() => {\n                const element = document.getElementById('nav-slider');\n                if (element) {\n                    this.cont = new Splide(\"#nav-slider\", {\n                        perMove: 3,\n                        rewind: true,\n                        pagination: false,\n                        isNavigation: true,\n                        arrows: true,\n                        autoWidth: true,\n                        breakpoints: {\n                            567: {\n                                arrows: false,\n                                wheel: true,\n                                direction: \"ltr\",\n                                releaseWheel: false,\n                            },\n                        },\n                    });\n                    this.cont.mount();\n                }\n            });\n        },\n        setinvi(data) {\n            this.invi = data;\n        },\n        setWidth() {\n            this.screenWidth = window.innerWidth;\n        },\n    },\n    created() {\n        this.$root.$on(\"updateinvi\", this.setinvi);\n    },\n    mounted() {\n        this.setWidth();\n        this.setSplider();\n\n        if (this.$refs[this.link]) {\n            this.$refs[this.link].scrollIntoView({\n                behavior: \"smooth\",\n                block: \"start\",\n                inline: \"nearest\",\n            });\n        }\n\n        window.addEventListener(\"resize\", () => {\n            this.setWidth();\n            if (this.cont) {\n                this.cont.destroy(true); // Destroy previous instance\n                this.setSplider();       // Re-initialize Splide\n            }\n        });\n    },\n};\n</script>\n\n<style scoped>\n.guest-invi-navbar-cont {\n    position: fixed;\n    display: flex;\n    justify-content: center;\n    top: 91px;\n    z-index: 9999999999;\n    width: 100%;\n}\n\n.guest-invi-navbar {\n    background: #461952;\n    border-radius: 27px;\n    padding: 8px 13px !important;\n}\n\n.guest-invi-navbar a {\n    font-family: \"Poppins\";\n    font-weight: 400;\n    font-size: 15px;\n    line-height: 22px;\n    text-align: center;\n    color: #9a9a9a;\n    padding: 8px 90px;\n    cursor: pointer;\n    white-space: nowrap;\n    text-decoration: none;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n\n.guest-invi-navbar a > span {\n    width: 46px;\n    height: 46px;\n    margin-bottom: 25px;\n    border-radius: 50%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    background: rgba(255, 255, 255, 0.1);\n}\n\n.guest-invi-navbar .active {\n    background: #ffffff;\n    border-radius: 23px;\n    color: #461952;\n    font-weight: 700;\n}\n\n.guest-invi-navbar > li {\n    border: none !important;\n}\n\n/* Mobile Styles */\n@media screen and (max-width: 768px) {\n    .guest-invi-navbar-cont {\n        top: 61px;\n    }\n\n    .guest-invi-navbar {\n        width: 100%;\n        max-width: 100% !important;\n        border-radius: 0 !important;\n    }\n\n    .guest-invi-navbar a {\n        padding: 8px 20px; /* Reduced padding from 90px to 20px */\n        font-size: 14px;\n    }\n\n    .guest-invi-navbar a > span {\n        width: 36px; /* Reduced from 46px */\n        height: 36px; /* Reduced from 46px */\n        margin-bottom: 10px; /* Reduced from 25px */\n    }\n\n    .guest-invi-navbar .active {\n        background: inherit;\n        border-radius: 0;\n        color: #ffffff;\n        font-weight: 700;\n    }\n\n    .guest-invi-navbar .active > span {\n        background-color: #ffffff;\n    }\n\n    .splide__track {\n        border-radius: 0 !important;\n    }\n    \n    /* Add compact spacing for splide slides */\n    .splide__slide {\n        padding: 0 5px !important;\n    }\n}\n\n/* Smaller screen fix */\n@media screen and (max-width: 576px) {\n    .guest-invi-navbar-cont {\n        top: 56px;\n    }\n    \n    .guest-invi-navbar a {\n        padding: 8px 10px; /* Further reduced padding for very small screens */\n        font-size: 12px;\n    }\n    \n    .guest-invi-navbar a > span {\n        width: 30px;\n        height: 30px;\n        margin-bottom: 8px;\n    }\n}\n\n/* Ensure PC styles don't get overridden */\n@media screen and (min-width: 769px) {\n    .guest-invi-navbar {\n        border-radius: 27px;\n    }\n\n    .guest-invi-navbar .active {\n        background: #ffffff;\n        color: #461952;\n        border-radius: 23px;\n    }\n\n    .guest-invi-navbar .active > span {\n        background-color: rgba(255, 255, 255, 0.1);\n    }\n}\n</style>\n\n"],"sourceRoot":""}]);
